* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}




.cart {

    .cartcontent {
        padding-top: 70px;

        .table {

            .table-head {
                .table-row {

                    :not(:nth-child(6)) {
                        padding-right: 15px !important;
                        padding-left: 15px !important;
                    }

                    .table-cell {
                        font-size: 30px;
                        padding: 20px 0;
                        background-color: #FF324D;
                        color: #fff;
                    }
                }
            }

            .table-row {

                > :not(:nth-child(5)) {
                    border-right: 1px solid #ddd;
                }

                > :not(:nth-child(5)) {
                    font-size: 25px;
                }

                .table-cell-1 {
                    width: 250px;

                    p {
                        margin-top: 20px;
                        font-size: 20px;
                    }
                    
                    img {
                        width: 100px;
                        height: 120px;
                    }
                }

                .delete {
                    span {
                        display: block;
                        cursor: pointer;
                        border-radius: 50%;
                        width: 50px;
                        height: 50px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin: 0 auto;
                        font-size: 30px;
                        transition: .4s;

                        &:hover {
                            background-color: #FF324D;
                            color: white;
                        }
                    }
                }

                .count {
                    div {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: 10px;

                        :nth-child(1) {
                            font-size: 40px;
                        }

                        button {
                            display: block;
                            cursor: pointer;
                            background-color: #ddd;
                            width: 50px;
                            height: 50px;
                            border-radius: 50%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            border: none;
                            outline: none;
                        }

                        span {
                            display: block;
                            cursor: pointer;
                            background-color: #ddd;
                            width: 50px;
                            height: 50px;
                            border-radius: 50%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }

                        :nth-child(2) {
                            border-radius: 10px;
                        }
                    }
                }
            }
        }
    }
}
















