* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}


// start media ---------------
.container {
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
}
  /* Small */
@media (min-width: 576px) {
    .container {
        width: 540px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
// xlarge
@media (min-width: 1200px) {
    .container {
        width: 90%;
    }
}
// End media ------------





.product-container {
    margin-top: 80px;

    .container {
        display: flex;
        gap: 50px;
        flex-wrap: wrap;
        padding-top: 20px;
        padding-bottom: 20px;

        .pro-img {
            flex-basis: 30%;
            display: flex;
            align-items: center;

            img {
                width: 100%;
                max-width: 100%;
                height: 100%;
                // border: 1px solid #ddd;
                padding: 5px;
            }
        }

        .pro-data {
            flex-basis: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;

            h3 {
                font-weight: bold;
            }

            .rate {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 20px;

                h4 {
                    color: #FF324D;
                    display: flex;
                    align-items: center;
                    gap: 10px;
    
                    span {
                        font-size: 15px;
                        color: #000;
                    }
                }

                .icons {
                    display: flex;
                    justify-content: center;
                    color: #F6BC3E;
                }
            }

            .description {
                color: #687188;
            }

            .sort {
                display: flex;
                flex-direction: column;
                gap: 10px;

                .sort-info {
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    :first-child {
                        color: #FF324D;
                    }
                }
            }

            button {
                width: 60%;
                margin: 0 auto;
                border: 1px solid #FF324D;
                background-color: transparent;
                border-radius: 5px;
                padding: 10px 20px;
                color: #FF324D;
                transition: .3s;
                position: relative;
                overflow: hidden;

                &:hover {
                    color: #fff;
                    &::after,
                    &::before {
                        width: 60%;
                    }
                }

                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 0;
                    height: 100%;
                    z-index: -1;
                    background-color: #FF324D;
                    transition: .4s;
                }

                &::before {
                    content: "";
                    position: absolute;
                    right: 0;
                    top: 0;
                    width: 0;
                    height: 100%;
                    z-index: -1;
                    background-color: #FF324D;
                    transition: .4s;
                }
            }
        }
    }

    // Start media
    @media (max-width:992px) {

        .pro-img {
            flex-basis: 40% !important;
        }   
    }

    @media (max-width:768px) {

        .container {
            flex-direction: column;

            .pro-img {
                width: fit-content;

                
                img {
                    width: 512px;
                }
            }
        }
    }   
}