* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}


// start media ---------------
.container {
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
}
  /* Small */
@media (min-width: 576px) {
    .container {
        width: 540px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
// xlarge
@media (min-width: 1200px) {
    .container {
        width: 90%;
    }
}
// End media ------------







// start path
.path {
    background-color: #f7f8fb;
    padding: 70px 0;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 15px;

        .path-head {
            font-size: 50px;
            font-weight: bold;
        }

        .path-links {

            .icon {
                font-size: 15px;
                margin: 0 10px;
            }

            span {
                transition: .4s;
                cursor: pointer;

                &:hover {
                    color: red;
                }
            }
        }
    }
}
// End path





// start Form
.form-login {

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 50px;
        padding-bottom: 50px;

        Form {
            width: 50%;
            padding: 5%;
            box-shadow: 0 0 10px rgba(0,0,0,0.2);
            border-radius: 10px;

            .form-head {
                font-size: 30px;
                font-weight: bold;
                margin-bottom: 30px;
            }

            .input-item {
                
                input {
                    font-size: 1.2rem;
                }

                .site-form-item-icon {
                    font-size: 20px;
                    margin-right: 10px;
                }
            }

            .remeber {
                
                .checkbox {
                    font-size: 20px;
                    transition: .4s;

                    &:hover {
                        color: blue;
                    }
                }

                > .ant-row 
                > .ant-col 
                > .ant-form-item-control-input 
                > .ant-form-item-control-input-content {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    > .login-form-forgot {
                        text-decoration: none;
                        color: #687188;
                        transition: .4s;
                        font-size: 20px;

                        &:hover {
                            color: #FF324D;
                        }
                    }
                } 
            }

            .submit {
                border: 1px solid #FF324D;
                border-radius: 10px;

                .login-form-button {
                    color: #FF324D;
                    font-size: 30px;
                    width: 100%;
                    height: 100%;
                    border-radius: 10px;
                    position: relative;
                    overflow: hidden;
                    border: none;
                    z-index: 1;
                    transition: .5s;

                    &:hover {
                        color: #fff;
                        &::before,
                        &::after {
                            width: 60%;
                        }
                    }

                    &::before {
                        content: "";
                        position: absolute;
                        top: 0;
                        left: 0;
                        background-color: #FF324D;
                        width: 0;
                        height: 100%;
                        z-index: -1;
                        transition: .5s;
                    }

                    &::after {
                        content: "";
                        position: absolute;
                        top: 0;
                        right: 0;
                        background-color: #FF324D;
                        width: 0;
                        height: 100%;
                        z-index: -1;
                        transition: .5s;
                    }
                }
            }

            .different-login {
                text-align: center;
                position: relative;
                margin: 20px 0;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 50%;
                    right: 0;
                    border-top: 1px solid #000;
                    transform: translateY(-50%);
                    z-index: -1;
                    height: 1px;
                    opacity: .3;
                }

                span {
                    background-color: #fff;
                    padding: 0 15px;
                    position: relative;
                    text-transform: uppercase;
                    font-size: 16px;
                }
            }

            .facgoog-login {
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 15px;
                flex-wrap: wrap;
                padding: 15px;

                .facebook,
                .google {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 10px;
                    background-color: #344E86;
                    color: #fff;
                    border-radius: 5px;
                    padding: 10px 20px; 
                    font-size: 20px;
                    text-decoration: none;
                    opacity: .9;
                    transition: .4s;

                    &:hover {
                        opacity: 1;
                    }
                }

                .google {
                    background-color: #D33D2B;
                }
            }

            .sinup {
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 15px;
                font-size: 20px;
                
                span {
                    opacity: .7;
                }

                a {
                    text-decoration: none;
                    color: #000;
                    transition: .3s;

                    &:hover {
                        color: #FF324D;
                    }
                }
            }
        }

        @media (max-width: 992px) {
            Form {
                width: 80%;
            }
        }

        @media (max-width: 768px) {
            Form {
                width: 100%;

                .remeber {
                    .checkbox,
                    .login-form-forgot {
                        font-size: 16px !important;
                    }
                }

                .sinup {
                    font-size: 16px !important;
                }
            }
        }
    }
}
// End Form