* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

/* Small */
@media (min-width: 576px) {
  .container {
    width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 90%;
  }
}
.product-container {
  margin-top: 80px;
}
.product-container .container {
  display: flex;
  gap: 50px;
  flex-wrap: wrap;
  padding-top: 20px;
  padding-bottom: 20px;
}
.product-container .container .pro-img {
  flex-basis: 30%;
  display: flex;
  align-items: center;
}
.product-container .container .pro-img img {
  width: 100%;
  max-width: 100%;
  height: 100%;
  padding: 5px;
}
.product-container .container .pro-data {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
}
.product-container .container .pro-data h3 {
  font-weight: bold;
}
.product-container .container .pro-data .rate {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.product-container .container .pro-data .rate h4 {
  color: #FF324D;
  display: flex;
  align-items: center;
  gap: 10px;
}
.product-container .container .pro-data .rate h4 span {
  font-size: 15px;
  color: #000;
}
.product-container .container .pro-data .rate .icons {
  display: flex;
  justify-content: center;
  color: #F6BC3E;
}
.product-container .container .pro-data .description {
  color: #687188;
}
.product-container .container .pro-data .sort {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.product-container .container .pro-data .sort .sort-info {
  display: flex;
  align-items: center;
  gap: 10px;
}
.product-container .container .pro-data .sort .sort-info :first-child {
  color: #FF324D;
}
.product-container .container .pro-data button {
  width: 60%;
  margin: 0 auto;
  border: 1px solid #FF324D;
  background-color: transparent;
  border-radius: 5px;
  padding: 10px 20px;
  color: #FF324D;
  transition: 0.3s;
  position: relative;
  overflow: hidden;
}
.product-container .container .pro-data button:hover {
  color: #fff;
}
.product-container .container .pro-data button:hover::after, .product-container .container .pro-data button:hover::before {
  width: 60%;
}
.product-container .container .pro-data button::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #FF324D;
  transition: 0.4s;
}
.product-container .container .pro-data button::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #FF324D;
  transition: 0.4s;
}
@media (max-width: 992px) {
  .product-container .pro-img {
    flex-basis: 40% !important;
  }
}
@media (max-width: 768px) {
  .product-container .container {
    flex-direction: column;
  }
  .product-container .container .pro-img {
    width: -moz-fit-content;
    width: fit-content;
  }
  .product-container .container .pro-img img {
    width: 512px;
  }
}/*# sourceMappingURL=singleproduct.css.map */