* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}


// start media ---------------
.container {
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
}
  /* Small */
@media (min-width: 576px) {
    .container {
        width: 540px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
// xlarge
@media (min-width: 1200px) {
    .container {
        width: 90%;
    }
}
// End media ------------








.furniture-home {

        // start swiper-----------
        .furnitureswiper {
            height: calc(100vh - 120px);
    
            &:hover {
                .swiper-button-prev,
                .swiper-button-next {
                    visibility: visible;
                    opacity: 1;
                }
            }
    
            .furnitur-slide-item {

                .slide-1, 
                .slide-2, 
                .slide-3 {
                    height: 100%;
                    background-position: center !important;
                    background-repeat: no-repeat !important;
                    background-size: cover !important;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: relative;
                    z-index: 55;

                    &::before {
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        height: 100%;
                        background-color: #000;
                        opacity: .5;
                        z-index: -1;
                    }
    
                    > .furnitur-item-head {
                        width: 60%;
                        text-align: center;
    
                        > h5 {
                            font-weight: 300;
                            margin-bottom: 1rem;
                            color: #fff;
                            font-size: 1.25rem;
                        }
    
                        > h2 {
                            font-size: 60px;
                            margin-bottom: 20px;
                            font-weight: 600;
                            color: #fff;
                        }

                        > p {
                            color: #fff;
                            font-size: 20px;
                        }
    
                        > a {
                            text-decoration: none;
                            display: block;
                            width: fit-content;
                            margin: 0 auto;
                            color: #fff;
                            border: 1px solid #fff;
                            border-radius: 10px;
                            position: relative;
                            padding: 10px 20px;
                            z-index: 19;
                            overflow: hidden;
                            transition: .4s;
                            font-size: 20px;
    
                            &:hover {
                                color: #000;
    
                                &::after,
                                &::before {
                                    width :60%;
                                }
                            } 
    
                            &::after {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                left: 0;
                                background-color: #fff;
                                z-index: -1;
                                transition: .4s;
                            }
    
                            &::before {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                right: 0;
                                background-color: #fff;
                                z-index: -1;
                                transition: .4s;
                            }
                        }
                    }
                }
    
                .slide-1 {
                    background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner10.jpg");
                }
                .slide-2 {
                    background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner12.jpg");
                }
                .slide-3 {
                    background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner11.jpg");
                }
            }
    
            .swiper-button-prev,
            .swiper-button-next {
                color: #fff;
                transition: .5s;
                width: 60px;
                height: 60px;
                border-radius: 6px;
                display: flex;
                justify-content: center;
                align-items: center;
                visibility: hidden;
                opacity: 0;
                
                &::after {
                    transition: .4s;
                }
                
                &:hover {
                    background-color: #fff;
                    
                    &::after {
                        color: #FF324D;
                    }
                }
            }
        }
        // End swiper-----------
    
        // start media for swiper
            @media (max-width : 992px) {
                .furnitureswiper {
                    height: 50vh;
    
                    .furnitur-slide-item {
                        height: 50vh;
                        
                        .furnitur-item-head {
    
                            h2 {
                                font-size: 50px !important;
                            }
                        }
                    }
                }
            }
    
            @media (max-width : 768px) {
                .furnitureswiper {
                    height: 40vh;
    
                    .furnitur-slide-item  {
                        height: 40vh;
                        
                        .furnitur-item-head {
                            width: 90% !important;
    
                            h2 {
                                font-size: 30px !important;
                            }
                        }
                    }
                }
            }
    
            @media (max-width : 580px) {
                .furnitureswiper {
                    .furnitur-slide-item  {
                        .furnitur-item-head {
                            h2 {
                                font-size: 30px !important;
                            }
    
                            p {
                                display: none;
                            }

                            h5 {
                                font-size: 1rem !important;
                            }
                        }
                    }
    
                    .swiper-button-prev,
                    .swiper-button-next {
                        display: none;
                    }
                }
            }
    
            @media (min-width:1200px) {
                .furnitur-item-head {
    
                    h5 {
                        font-weight: 300;
                        font-size: 2.5rem !important;
                    }
    
                    h2 {
                        font-size: 4rem !important;
                    }
    
                    p {
                        font-size: 1rem !important;
                    }
    
                    a {
                        font-size: 1rem !important;
                    }
                }
            }    
            // End media for swiper-----
        // End swiper--------------------------------------------------------




        // start slider 2----------------------------------
        .furniture-swiber-2 {
            width: 70%;
            display: flex;
            position: relative;
            background-color: #fff;
            box-shadow: 0 0 10px rgba(0,0,0,0.2);
            padding: 30px;
            z-index: 1;
            margin-top: -70px;
            margin-right: auto;
            margin-left: auto;
            border-radius: 6px;

            &:hover {
                .swiper-button-prev,
                .swiper-button-next {
                    visibility: visible;
                    opacity: 1;
                }

                .swiper-button-prev {
                    left: 5px;
                }

                .swiper-button-next {
                    right: 5px;
                }
            }

            .box-1 {
                flex-basis: 30%;

                h4 {
                    color: #292b2c;
                }

                p {
                    color: #687188;
                }

                a {
                    text-decoration: none;
                    display: block;
                    width: fit-content;
                    color: #000;
                    border: 1px solid #000;
                    border-radius: 10px;
                    position: relative;
                    padding: 10px 20px;
                    z-index: 19;
                    overflow: hidden;
                    transition: .4s;
                    font-size: 16px;

                    &:hover {
                        color: #fff;

                        &::after,
                        &::before {
                            width :60%;
                        }
                    } 

                    &::after {
                        content: "";
                        position: absolute;
                        width: 0;
                        height: 100%;
                        top: 0;
                        left: 0;
                        background-color: #000;
                        z-index: -1;
                        transition: .4s;
                    }

                    &::before {
                        content: "";
                        position: absolute;
                        width: 0;
                        height: 100%;
                        top: 0;
                        right: 0;
                        background-color: #000;
                        z-index: -1;
                        transition: .4s;
                    }
                }
            }

            .box-2-swiber {
                flex-basis: calc(70% - 20px);
                position: relative;

                .box-2-slide-item {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: #F7F7F7;
                    padding: 20px;

                    &:hover {
                        .slide-1 {
                            color: #FF324D;

                            :nth-child(1) {
                                color: #FF324D;
                            }
                        }
                    }

                    .slide-1 {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        gap: 15px;
                        font-size: 25px;
                        transition: .3s;

                        :nth-child(1) {
                            color: #292b2c;
                            font-size: 60px;
                            transition: .3s;
                        }
                    }
                }
            }

            .swiper-button-prev,
            .swiper-button-next {
                color: #000;
                transition: .5s;
                width: 40px;
                height: 40px;
                border-radius: 6px;
                background-color: #fff;
                position: absolute;
                box-shadow: 0 0 10px rgba(0,0,0,0.2);
                top: 50%;
                visibility: hidden;
                opacity: 0;
                
                &::after {
                    transition: .4s;
                    font-size: 25px;
                }
                
                &:hover {
                    
                    &::after {
                        color: #FF324D;
                    }
                }
            }

            .swiper-button-next {
                right: -40px;
            }

            .swiper-button-prev {
                left : -40px;
            }
        }

            // start media slider 2---
            @media (max-width:991px) {
                .furniture-swiber-2 {
                    width: 80%;
                    margin-top: -30px;
                }
            }

            @media (max-width:768px) {
                .furniture-swiber-2 {
                    width: 85% !important;
                    margin-top: -20px !important;
                    flex-direction: column;
                    gap: 25px;

                    .box-1 {
                        text-align: center;

                        a {
                            margin: 0 auto;
                        }
                    }

                    .box-2-swiber {
                        width: 100% !important;
                    }
                }
            }
            // End media slider 2---
        // End slider 2----------------------------------




        // start main----------------------------------------------------
        main {

            .furniture1-section-2 {
                padding: 100px 0;
                background: url("https://bestwebcreator.com/shopwise/demo/assets/images/furniture_banner3.jpg");
                background-position: center center;
                background-size: cover;

                .container {

                    .furniture1-banner {
                        width: fit-content;

                        h3 {
                            font-weight: bold;
                            font-size: 32px;
                            color: #292b2c;
                        }

                        h4 {
                            font-size: 80px;
                            margin-bottom: 15px;
                            color: #FF324D;
                        }

                        .count {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            gap: 15px;

                            .count-down {
                                background-color: #fff;
                                padding: 15px;
                                box-shadow: 0 0 5px rgba(0,0,0,0.03);
                                text-align: center;
                                flex-basis: calc(26% - 15px);
                                border-radius: 6px;

                                h5 {
                                    font-weight: bold;
                                    font-size: 40px;
                                }
                            }
                        }

                        a {
                            text-decoration: none;
                            display: block;
                            margin-top: 50px;
                            width: fit-content;
                            color: #FF324D;
                            border: 1px solid #FF324D;
                            border-radius: 10px;
                            position: relative;
                            padding: 15px 30px;
                            z-index: 19;
                            overflow: hidden;
                            transition: .4s;
                            font-size: 30px;
        
                            &:hover {
                                color: #fff;
        
                                &::after,
                                &::before {
                                    width :60%;
                                }
                            } 
        
                            &::after {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                left: 0;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .4s;
                            }
        
                            &::before {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                right: 0;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .4s;
                            }
                        }
                    }
                }

                // Start media
                @media (max-width:600px) {
                    .furniture1-banner {
                        margin: 0 auto;

                        h3 {
                            font-size: 22px !important;
                        }

                        h4 {
                            font-size: 50px !important;
                        }

                        a {
                            font-size: 20px !important;
                            padding: 10px 20px !important;
                        }

                        .count-down {
                            padding: 10px !important;
                            width: 25% !important;

                            h5 {
                                font-size: 30px !important;
                            }
                        }
                    }
                } 
                // End media 
            }

            .furniture1-section-3 {
                padding: 80px 0 0;

                .title {
                    text-align: center;

                    p {
                        margin-bottom: 30px;
                        color: #687188;
                    }
                }

                .container {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 15px;
                    flex-wrap: wrap;

                    .boxes {
                        flex-basis: calc((100% / 3) - 15px);
                        height: 500px;
                        box-shadow: 0 0 10px rgba(0,0,0,0.1);
                        border-radius: 10px;

                        &:hover {
                            img {
                                transform: scale(1.1);
                            }
                        }

                        .boxes-img {
                            overflow: hidden;

                            img {
                                max-width: 100%;
                                width: 100%;
                                transition: .4s;
                            }
                        }

                        .blog {
                            padding: 10px 20px;

                            a {
                                text-decoration: none;
                                color: #292b2c;
                                font-weight: bold;
                                transition: .4s;
                                font-size: 18px;

                                &:hover {
                                    color: #FF324D;
                                }
                            }

                            .blog-icons {
                                margin-bottom: 5px;
                                display: flex;
                                align-items: center;
                                gap: 15px;
                                margin: 15px 0;

                                div {

                                    a {
                                        display: flex;
                                        align-items: center;
                                        font-weight: normal;

                                        > :nth-child(1) {
                                            margin-right: 10px;
                                            color: #FF324D;
                                        }
                                    }
                                }
                            }

                            p {
                                color: #687188;
                            }
                        }
                    }
                }

                // start media 
                @media (max-width:1200px) {
                    .container {

                        .boxes {
                            flex-basis: calc((100% / 3) - 10px);
                            height: 450px;

                            .blog-icons {
                                a {
                                    font-size: 16px !important;
                                }
                            }
                        }
                    }
                }

                @media (max-width:991px) {
                    .container {

                        .boxes {
                            flex-basis: calc((100% / 2) - 10px);
                            height: 450px;
                        }
                    }
                }

                @media (max-width:768px) {
                    .container {
                        gap: 50px;

                        .boxes {
                            flex-basis: 100% !important;
                            height: fit-content !important;
                        }
                    }
                }
                // End media 
            }
        }
        // End main----------------------------------------------------
}