.wish-con {
  padding: 70px 0;
}
.wish-con .container {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
}
.wish-con .container .box {
  width: 250px;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
  position: relative;
}
.wish-con .container .box img {
  width: 100%;
  max-width: 100%;
  border-radius: 10px;
}
.wish-con .container .box h4 {
  margin: 10px 0;
}
.wish-con .container .box .icon {
  position: absolute;
  top: 10px;
  right: 10px;
  border: 1px solid #ddd;
  color: orange;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-size: 25px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.4s;
}
.wish-con .container .box button {
  border: 1px solid #FF324D;
  background-color: transparent;
  border-radius: 30px;
  padding: 10px 20px;
  margin-top: 15px;
  color: #FF324D;
  font-size: 20px;
  position: relative;
  overflow: hidden;
  z-index: 10;
  transition: 0.3s;
  display: block;
  width: 180px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
}
.wish-con .container .box button:hover {
  color: #fff;
}
.wish-con .container .box button:hover::after, .wish-con .container .box button:hover::before {
  width: 60%;
}
.wish-con .container .box button::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  background-color: #FF324D;
  z-index: -1;
  transition: 0.3s;
}
.wish-con .container .box button::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 100%;
  background-color: #FF324D;
  z-index: -1;
  transition: 0.3s;
}/*# sourceMappingURL=wishlist.css.map */