* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}


// start media ---------------
.container {
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
}
  /* Small */
@media (min-width: 576px) {
    .container {
        width: 540px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
// xlarge
@media (min-width: 1200px) {
    .container {
        width: 90%;
    }
}
// End media ------------






.wish-con {
    padding: 70px 0;

    .container {
        display: flex;
        gap: 15px;
        flex-wrap: wrap;
        justify-content: center;


        .box {
            width: 250px;
            height: fit-content;
            padding: 10px;
            border: 1px solid #ddd;
            border-radius: 10px;
            position: relative;

            img {
                width: 100%;
                max-width: 100%;
                border-radius: 10px;
            }

            h4 {
                margin: 10px 0;
            }

            .icon {
                position: absolute;
                top: 10px;
                right: 10px;
                border: 1px solid #ddd;
                color: orange;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 5px;
                font-size: 25px;
                border-radius: 10px;
                cursor: pointer;
                transition: .4s;
            }

            button {
                border: 1px solid #FF324D;
                background-color: transparent;
                border-radius: 30px;
                padding: 10px 20px;
                margin-top: 15px;
                color: #FF324D;
                font-size: 20px;
                position: relative;
                overflow: hidden;
                z-index: 10;
                transition: .3s;
                display: block;
                width: 180px;
                margin-left: auto;
                margin-right: auto;
                margin-top: 15px;

                &:hover {
                    color: #fff;

                    &::after,
                    &::before {
                        width: 60%;
                    }
                }

                &::after {
                    content:'';
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 0;
                    height: 100%;
                    background-color: #FF324D;
                    z-index: -1;
                    transition: .3s;
                }

                &::before {
                    content:'';
                    position: absolute;
                    right: 0;
                    top: 0;
                    width: 0;
                    height: 100%;
                    background-color: #FF324D;
                    z-index: -1;
                    transition: .3s;
                }
            }
        }
    }
}