* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}


// start media ---------------
.container {
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
}
  /* Small */
@media (min-width: 576px) {
    .container {
        width: 540px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
// xlarge
@media (min-width: 1200px) {
    .container {
        width: 90%;
    }
}
// End media ------------









.page-con {

    // start swiper-----------
    .swiper {
        height: calc(100vh - 120px);

        &:hover {
            .swiper-button-prev,
            .swiper-button-next {
                visibility: visible;
                opacity: 1;
            }
        }

        .slide-item {
            
            .slide-1, 
            .slide-2, 
            .slide-3 {
                height: 100%;
                background-position: center !important;
                background-repeat: no-repeat !important;
                background-size: cover !important;
                position: relative;

                .item-head {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 100px;

                    h5 {
                        font-weight: 300;
                        margin-bottom: 1rem;
                    }

                    h2 {
                        font-size: 60px;
                        margin-bottom: 20px;
                        font-weight: 600;
                    }

                    a {
                        text-decoration: none;
                        display: block;
                        width: fit-content;
                        color: #FF324D;
                        border: 1px solid #FF324D;
                        border-radius: 10px;
                        position: relative;
                        padding: 15px 30px;
                        z-index: 19;
                        overflow: hidden;
                        transition: .4s;
                        font-size: 20px;

                        &:hover {
                            color: #fff;

                            &::after,
                            &::before {
                                width :60%;
                            }
                        } 

                        &::after {
                            content: "";
                            position: absolute;
                            width: 0;
                            height: 100%;
                            top: 0;
                            left: 0;
                            background-color: #FF324D;
                            z-index: -1;
                            transition: .4s;
                        }

                        &::before {
                            content: "";
                            position: absolute;
                            width: 0;
                            height: 100%;
                            top: 0;
                            right: 0;
                            background-color: #FF324D;
                            z-index: -1;
                            transition: .4s;
                        }
                    }
                }
            }

            .slide-1 {
                background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner1.jpg");
            }
            .slide-2 {
                background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner2.jpg");
            }
            .slide-3 {
                background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner3.jpg");
            }
        }

        .swiper-button-prev,
        .swiper-button-next {
            color: #000;
            transition: .5s;
            width: 60px;
            height: 60px;
            border-radius: 6px;
            display: flex;
            justify-content: center;
            align-items: center;
            visibility: hidden;
            opacity: 0;
            
            &::after {
                transition: .4s;
            }
            
            &:hover {
                background-color: #fff;
                
                &::after {
                    color: #FF324D;
                }
            }
        }
    }
    // End swiper-----------

    // start media for swiper
        @media (max-width : 992px) {
            .swiper {
                height: 50vh;

                .slide-item {
                    height: 50vh;
                    
                    .item-head {

                        h2 {
                            font-size: 50px !important;
                        }
                    }
                }
            }
        }

        @media (max-width : 768px) {
            .swiper {
                height: 40vh;

                .slide-item  {
                    height: 40vh;
                    
                    .item-head {
                        left: 70px !important;

                        h2 {
                            font-size: 40px !important;
                        }
                    }
                }

                .owl-nav {
                    display: none ;
                }
            }
        }

        @media (max-width : 580px) {
            .swiper {
                height: 30vh;

                .slide-item  {
                    height: 30vh;
                    
                    .item-head {
                        left: 40px !important;

                        p {
                            width: 200px;
                        }

                        h2 {
                            font-size: 30px !important;
                        }

                        h5 {
                            font-size: 1rem !important;
                            width: 180px;
                        }

                        a {
                            font-size: 16px !important;
                            padding: 10px 20px !important;
                        }
                    }
                }

                .swiper-button-prev,
                .swiper-button-next {
                    display: none;
                }
            }
        }

        @media (min-width:1200px) {
            .item-head {

                h5 {
                    font-weight: 300;
                    font-size: 3rem !important;
                }

                h2 {
                    font-size: 4rem !important;
                }

                p {
                    font-size: 1rem !important;
                }

                a {
                    font-size: 1rem !important;
                }
            }
        }    
        // End media for swiper-----
    // End swiper--------------------------------------------------------










// start main----------------------------------------------------------    
    main {

        // Start section-1---
        .section-1 {

            .container {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                gap: 15px;

                .boxes {
                    position: relative;
                    overflow: hidden;
                    flex: 1;

                    &:hover {
                        img {
                            transform: scale(1.1);
                        }
                    }

                    img {
                        width: 100%;
                        max-width: 100%;
                        transition: .3s;
                    }
    
                    .box-text {
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 30px;

                        h5 {
                            font-size: 25px;
                            color: #292b2c;
                            margin-bottom: 0.5rem;
                        }

                        h3 {
                            font-size: 30px;
                            color: #292b2c;
                            margin-bottom: 0.5rem;
                            font-weight: bold;
                        }

                        a {
                            color: #292b2c;
                            transition: .4s;
                            position: relative;
                            text-decoration: none;
                            font-size: 20px;
                            margin-left: 6px;

                            &:hover {
                                color: #FF324D;
                                &::after {
                                    width: 100%;
                                }
                            } 

                            &::after {
                                content: "";
                                position: absolute;
                                bottom: -5px;
                                width: 40%;
                                height: 2px;
                                left: 50%;
                                transform: translateX(-50%);
                                background-color: #FF324D;
                                transition: .4s;
                            }
                        }
                    }
                }
            }

            // start media ------
            @media (max-width:991px) {
                .boxes {
                    flex-basis: 100% !important;
                }
            }
            @media (max-width:580px) {
                .box-text {
                    right: 15px !important;

                    h5 {
                        font-size: 20px !important;
                    }
                    
                    h3 {
                        font-size: 23px !important;
                    }
                }
            }
            // End media------
        }
        // End section-1---


        // Start section-2---
        .section-2 {
            padding: 80px 0;

            // Start head-text

            .head-text {
                margin-bottom: 40px;

                h2 {
                    position: relative;
                    padding-bottom: 20px;
                    width: fit-content;
                    margin: 0 auto;

                    &::after {
                        content: "";
                        position: absolute;
                        left: 50%;
                        bottom: 0;
                        transform: translateX(-50%);
                        width: 30%;
                        height: 3px;
                        background-color: #FF324D;
                        border-radius: 10px;
                    }
                }
            }
            // End head-text

            // Start container 
            .container {
                display: grid;
                grid-template-columns: repeat(auto-fill , minmax(250px , 1fr));
                gap: 20px;
                padding: 20px;

                .pro-box {
                    border: 1px solid #ddd;
                    border-radius: 10px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: space-around;
                    position: relative;
                    overflow: hidden;

                    &:hover {

                        .over-div {
                            left: 0;
                        }
                    }


                    .over-div {
                        position: absolute;
                        top: 0;
                        left: -100%;
                        width: 100%;
                        height: 100%;
                        background-color: #687188b2;
                        transition: .5s;
                        overflow: hidden;

                        .icons {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            height: 70%;
                            gap: 20px;

                            .color-icon {
                                transition: .3s;
                                &:hover {
                                    background-color: #FF324D;
                                    color: #fff;

                                    a {
                                        color: #fff;
                                    }
                                }
                            }

                            .color-icon {
                                background-color: #fff;
                                border-radius: 50%;
                                padding: 10px;
                                cursor: pointer;

                                a {
                                    color: #000;
                                }
                            }
                        }

                        button {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            gap: 15px;
                            border: 1px solid #fff;
                            background-color: transparent;
                            border-radius: 30px;
                            padding: 10px 20px;
                            margin: 0 auto;
                            color: #fff;
                            font-size: 20px;
                            position: relative;
                            overflow: hidden;
                            z-index: 10;
                            transition: .3s;

                            &:hover {
                                border-color: #FF324D;

                                &::after,
                                &::before {
                                    width: 60%;
                                }
                            }

                            &::after {
                                content:'';
                                position: absolute;
                                left: 0;
                                width: 0;
                                height: 100%;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .3s;
                            }

                            &::before {
                                content:'';
                                position: absolute;
                                right: 0;
                                width: 0;
                                height: 100%;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .3s;
                            }
                        }
                    }

                    .pro-img {
                        width: 100%;
                        height: 180px;
                        text-align: center;
                        padding-top: 15px;

                        img {
                            width: 85%;
                            height: 100%;
                            background-color: transparent;
                            border-radius: 10px;
                        }
                    }

                    .pro-text {
                        padding: 10px;
                        text-align: center;

                        h6 {
                            margin-top: 10px;
                            color: brown;
                        }

                        h5 {
                            font-weight: bold;
                            margin: 15px 0;
                        }

                        p {
                            color: #FF324D;
                            font-weight: bold;
                            font-size: 20px;

                            span {
                                margin-left: 15px;
                                color: #000;
                                font-weight: normal;
                            }
                        }

                        .rate {
                            color: #F6BC3E;
                            
                        }
                    }
                }
            }
            // End container 
        }
        // End section-2---


        // Start section-3---
        .section-3 {
            background-color: #F4F9FC;
            padding: 0;

            .container {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                gap: 25px;

                .img-div {
                    flex-basis: calc(40% - 25px);
                    margin-top: -40px;

                    img {
                        width: 100%;
                        max-width: 100%;
                    }
                }

                .box-text {
                    flex-basis: calc(60% - 25px);

                    > div {
                        width: fit-content;
                        margin: 0 auto;

                        p {
                            color: #FF324D;
                            margin-bottom: 10px;
                            font-size: 20px;
                        }
    
                        h2 {
                            font-weight: 700;
                            margin-bottom: 1.5rem;
                        }
    
                        h5 {
                            margin-bottom: 1.5rem;
                        }
    
                        a {
                            text-decoration: none;
                            display: block;
                            width: fit-content;
                            color: #FF324D;
                            border: 1px solid #FF324D;
                            border-radius: 10px;
                            position: relative;
                            padding: 10px 20px;
                            z-index: 19;
                            overflow: hidden;
                            transition: .4s;
                            font-size: 16px;
    
                            &:hover {
                                color: #fff;
    
                                &::after,
                                &::before {
                                    width :60%;
                                }
                            } 
    
                            &::after {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                left: 0;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .4s;
                            }
    
                            &::before {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                right: 0;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .4s;
                            }
                        }
                    }
                }
            }

            // start media 
            @media (max-width:768px) {
                .container {
                    flex-direction: column;
                    gap: 40px;
                    padding-bottom: 20px;

                    .img-div ,
                    .box-text {
                        flex-basis: 100%;
                    }
                }
            }
            // End media 
        }
        // End section-3---


        // Start section-4----
        .fsection-4 {
            padding: 80px 0;

            .container {

                .head-text {
                    margin-bottom: 40px;
        
                    h2 {
                        position: relative;
                        padding-bottom: 20px;
                        width: fit-content;
                        margin: 0 auto;
        
                        &::after {
                            content: "";
                            position: absolute;
                            left: 50%;
                            bottom: 0;
                            transform: translateX(-50%);
                            width: 30%;
                            height: 3px;
                            background-color: #FF324D;
                            border-radius: 10px;
                        }
                    }
                }

                .swiper {
                    padding: 15px 0;
                    position: relative;
                    overflow-y: visible;
                    height: fit-content !important;
    
                    .slide-item {
                        border: 1px solid #ddd;
                        border-radius: 10px;
                        height: 430px !important;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        flex-direction: column;
                        gap: 40px;
                        padding: 25px 0;
        
                        &:hover {
                            img {
                                transform: scale(1.1);
                            }
                        }
        
                        img {
                            width: 80%;
                            height: 180px;
                            transition: .4s;
                        }
        
                        .rate {
                            text-align: center;
                            width: 95%;
                            padding: 0 15px;
        
                            button {
                                border: 1px solid #FF324D;
                                outline: none;
                                border-radius: 10px;
                                background-color: transparent;
                                width: 100%;
                                padding: 5px 10px;
                                transition: .4s;
                                color: #FF324D;
                                position: relative;
                                z-index: 20;
                                overflow: hidden;
        
                                &:hover {
                                    color: #fff;
                                    &::after,
                                    &::before {
                                        width: 60%;
                                    }
                                }
        
                                &::after {
                                    content: "";
                                    position: absolute;
                                    left: 0;
                                    top: 0;
                                    width: 0;
                                    height: 100%;
                                    background-color: #FF324D;
                                    transition: .4s;
                                    z-index: -1;
                                }
        
                                &::before {
                                    content: "";
                                    position: absolute;
                                    right: 0;
                                    top: 0;
                                    width: 0;
                                    height: 100%;
                                    background-color: #FF324D;
                                    transition: .4s;
                                    z-index: -1;
                                }
                            }
        
                            h4 {
                                color: #FF324D;
                                margin-bottom: 15px;
                            }
                        }
                    }
        
                    .swiper-button-prev {
                        color: #000;
                        position: absolute;
                        left: 0;
                        top: -30px;
                        visibility: visible;
                        opacity: 1;
                        
                        &::after {
                            transition: .4s;
                        }
                        
                        &:hover {
                            color: #FF324D;
                        }
                    }
        
                    .swiper-button-next {
                        color: #000;
                        position: absolute;
                        right: 0;
                        top: -30px;
                        visibility: visible;
                        opacity: 1;
    
                        &::after {
                            transition: .4s;
                        }
        
                        &:hover {
                            color: #FF324D;
                        }
                    }
                }
            }
        }
        // End section-4


        // Start section-5----
        .section-5 {
            padding: 80px 0;
            background-color: #FFF1F1;

            .container {

                .head-text {
                    margin-bottom: 20px;
        
                    h2 {
                        position: relative;
                        padding-bottom: 20px;
                        width: fit-content;
                        margin: 0 auto;
        
                        &::after {
                            content: "";
                            position: absolute;
                            left: 50%;
                            bottom: 0;
                            transform: translateX(-50%);
                            width: 30%;
                            height: 3px;
                            background-color: #FF324D;
                            border-radius: 10px;
                        }
                    }
                }

                .swiper {
                    height: 50vh;

                    .swiper-slide-item {
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        .slide {
                            width: 70%;
                            text-align: center;

                            p {
                                color: #687188;
                                font-size: 20px;
                            }

                            .boxes {
                                margin-top: 40px !important;
                                display: flex;
                                align-items: center;
                                gap: 20px;
                                width: fit-content;
                                margin: 0 auto;

                                .boxes-names {
                                    
                                    :nth-child(2) {
                                        color: #FF324D;
                                    }
                                }
                            }
                        }
                    }   
                }
            }
        }
        // End section-5----


        // Start section-6----
        .section-6 {
            padding-top: 80px;

            .container {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 20px;

                .boxes {
                    flex-basis: calc(30% - 20px);
                    text-align: center;
                    position: relative;

                    &:not(:nth-child(3)) {
                        
                        &::after {
                            content: "";
                            position: absolute;
                            right: -40px;
                            top: 50%;
                            transform: translateY(-50%);
                            height: 60%;
                            width: 2px;
                            background-color: #ddd;
                        }
                    }

                    h5 {
                        margin: 10px 0;
                        color: #292b2c;
                        font-size: 1.5rem;
                    }

                    .icon {
                        font-size: 40px;
                        color: #FF324D;
                    }
                }
            }

            // start media
            @media (max-width:768px) {
                .container {
                    flex-direction: column;

                    .boxes {
                        flex-basis: 100%;

                        &:nth-child(2) {
                            border-top: 1px solid #ddd;
                            border-bottom: 1px solid #ddd;
                            padding: 30px 0;
                        }

                        &::after {
                            display: none;
                        }
                    }
                }
            }
            // End media
        }
        // End section-6----
    }
}