* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

/* Small */
@media (min-width: 576px) {
  .container {
    width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 90%;
  }
}
.path {
  background-color: #f7f8fb;
  padding: 70px 0;
}
.path .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}
.path .container .path-head {
  font-size: 50px;
  font-weight: bold;
}
.path .container .path-links .icon {
  font-size: 15px;
  margin: 0 10px;
}
.path .container .path-links span {
  transition: 0.4s;
  cursor: pointer;
}
.path .container .path-links span:hover {
  color: red;
}

.form-login .container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
.form-login .container Form {
  width: 50%;
  padding: 5%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.form-login .container Form .form-head {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 30px;
}
.form-login .container Form .input-item input {
  font-size: 1.2rem;
}
.form-login .container Form .input-item .site-form-item-icon {
  font-size: 20px;
  margin-right: 10px;
}
.form-login .container Form .remeber .checkbox {
  font-size: 20px;
  transition: 0.4s;
}
.form-login .container Form .remeber .checkbox:hover {
  color: blue;
}
.form-login .container Form .remeber > .ant-row > .ant-col > .ant-form-item-control-input > .ant-form-item-control-input-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form-login .container Form .remeber > .ant-row > .ant-col > .ant-form-item-control-input > .ant-form-item-control-input-content > .login-form-forgot {
  text-decoration: none;
  color: #687188;
  transition: 0.4s;
  font-size: 20px;
}
.form-login .container Form .remeber > .ant-row > .ant-col > .ant-form-item-control-input > .ant-form-item-control-input-content > .login-form-forgot:hover {
  color: #FF324D;
}
.form-login .container Form .submit {
  border: 1px solid #FF324D;
  border-radius: 10px;
}
.form-login .container Form .submit .login-form-button {
  color: #FF324D;
  font-size: 30px;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  border: none;
  z-index: 1;
  transition: 0.5s;
}
.form-login .container Form .submit .login-form-button:hover {
  color: #fff;
}
.form-login .container Form .submit .login-form-button:hover::before, .form-login .container Form .submit .login-form-button:hover::after {
  width: 60%;
}
.form-login .container Form .submit .login-form-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #FF324D;
  width: 0;
  height: 100%;
  z-index: -1;
  transition: 0.5s;
}
.form-login .container Form .submit .login-form-button::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  background-color: #FF324D;
  width: 0;
  height: 100%;
  z-index: -1;
  transition: 0.5s;
}
.form-login .container Form .different-login {
  text-align: center;
  position: relative;
  margin: 20px 0;
}
.form-login .container Form .different-login::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  right: 0;
  border-top: 1px solid #000;
  transform: translateY(-50%);
  z-index: -1;
  height: 1px;
  opacity: 0.3;
}
.form-login .container Form .different-login span {
  background-color: #fff;
  padding: 0 15px;
  position: relative;
  text-transform: uppercase;
  font-size: 16px;
}
.form-login .container Form .facgoog-login {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
  padding: 15px;
}
.form-login .container Form .facgoog-login .facebook,
.form-login .container Form .facgoog-login .google {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #344E86;
  color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 20px;
  text-decoration: none;
  opacity: 0.9;
  transition: 0.4s;
}
.form-login .container Form .facgoog-login .facebook:hover,
.form-login .container Form .facgoog-login .google:hover {
  opacity: 1;
}
.form-login .container Form .facgoog-login .google {
  background-color: #D33D2B;
}
.form-login .container Form .sinup {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  font-size: 20px;
}
.form-login .container Form .sinup span {
  opacity: 0.7;
}
.form-login .container Form .sinup a {
  text-decoration: none;
  color: #000;
  transition: 0.3s;
}
.form-login .container Form .sinup a:hover {
  color: #FF324D;
}
@media (max-width: 992px) {
  .form-login .container Form {
    width: 80%;
  }
}
@media (max-width: 768px) {
  .form-login .container Form {
    width: 100%;
  }
  .form-login .container Form .remeber .checkbox,
  .form-login .container Form .remeber .login-form-forgot {
    font-size: 16px !important;
  }
  .form-login .container Form .sinup {
    font-size: 16px !important;
  }
}/*# sourceMappingURL=loginpage.css.map */