* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
} 

html {
    scroll-behavior: smooth;
}


body::-webkit-scrollbar {
    width: 6px;
}

body::-webkit-scrollbar-thumb {
    background-color: #FF324D;
    border-radius: 20px;
}




body {
    font-family: 'Poppins', sans-serif;
}

// start variables-------------
$header-font-size : 14px;
$header-text-color : #292b2c;
$header-hover-color : #FF324D;
// End variables---------------


// start media ---------------
.container {
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
}
  /* Small */
@media (min-width: 576px) {
    .container {
        width: 540px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
// xlarge
@media (min-width: 1200px) {
    .container {
        width: 90%;
    }
}
// End media ------------






// start header----------------------------------------------------------
header {

    // Start top-header div
    .top-header {
        padding: 10px 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.219);

        // start container
        .container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
            
            /* Start number-lang */
            .number-lang {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 15px;

                /* Start langauge select */
                .select-1 {
                    width: fit-content;

                    .ant-select-selector {
                        border: none;
                    }

                    img {
                        border-radius: 50%;
                        width: 20px;
                    }
                    .align-list {
                        display: flex;
                        align-items: center;
                        gap: 10px;
                    }
                }
                /* End langauge select */

                // Start currency
                .currency {
                    .select-2 {
                        width: fit-content;

                        .ant-select-selector {
                            border: none;
                        }
                    }
                }
                // End currency

                // start num
                .num {
                    display: flex;
                    gap: 5px;
                    align-items: center;
                    font-size: $header-font-size;
                }
                // End num
            }
            /* End number-lang */

            /* Start login */
            .login {
                // border: 1px solid green solid purple;
                display: flex;
                align-items: center;
                gap: 20px;

                // Start shuffle, wishlist, user
                .shuffle:hover,
                .wishlist:hover,
                .user:hover {

                    a {
                        color : $header-hover-color;
                    }
                }

                .shuffle a,
                .wishlist a,
                .user a {
                    text-decoration: none;
                    color: $header-text-color;
                    display: flex;
                    align-items: center;
                    gap: 5px;
                    transition: .5s;

                    span {
                        font-size: $header-font-size;
                    }
                }
                // End shuffle, wishlist, user
            }
            /* End login */
        }
        // End container

        // media for Top Header
        @media (max-width : 768px) {
            .container {
                justify-content: center;
                gap: 30px;
            }
        }
    }
    // End top-header div


    // Start bottom-header div
    .bottom-header {
        padding: 10px 0;

        // Start container
        .container {
            display: flex;
            justify-content: space-between;
            gap: 20px;

            // Start nav
            nav {
                display: block;
                z-index: 95;

                .active {
                    color: $header-hover-color;
                }

                ul {
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    list-style: none;
                    margin: 0 ;
                    height: 100%;

                    li {
                        display: flex;
                        align-items: center;
                        height: 100%;
                    }

                    li:hover > a{
                        color: $header-hover-color;
                    }

                    li > .main-list-tap,
                    li > a {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: 5px;
                        text-decoration: none;
                        color: $header-text-color;
                        font-size: 20px;
                        transition: .3s;
                    }

                    // Start Home , pages Links
                    .home-con,
                    .pages-con  {
                        position: relative;

                        &:hover {
                            .home-links,
                            .Pages-links {
                                bottom: -260px;
                                opacity: 1;
                                visibility: visible;
                            }
                        }

                        .home-links,
                        .Pages-links {
                            position: absolute;
                            bottom: -290px;
                            left: -10px;
                            width: 200px;
                            background-color: #fff;
                            opacity: 0;
                            visibility: hidden;
                            transition: .3s;
                            box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);

                            > ul {
                                display: flex;
                                flex-flow: column;
                                align-items: flex-start;
                                gap: 10px;
                                list-style: none;
                                margin: 0;
                                padding: 10px;

                                > li {
                                    width: 100%;

                                    > a {
                                        transition: .5s;
                                    }

                                    &:hover {
                                        > a {
                                            color: $header-hover-color;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    // End Home , pages Links

                    // Edit pages Links width
                    .pages-con {
                        .Pages-links {
                            width: 240px;
                        }
                    }
                    // Edit pages Links width

                    // start products links
                    .produts-con {
                        position: relative;

                        &:hover {
                            .products-links {
                                bottom: -428px;
                                opacity: 1;
                                visibility: visible;
                            }
                        }

                        .products-links {
                            position: absolute;
                            bottom: -450px;
                            left: -470px;
                            width: 950px;
                            background-color: #fff;
                            opacity: 0;
                            visibility: hidden;
                            transition: .3s;
                            padding: 20px;
                            box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);

                            // Start pro-collection
                            .pro-collection {
                                display: grid;
                                grid-template-columns: repeat(auto-fill , minmax(210px , 1fr));
                                gap: 10px;

                                > ul:not(:first-child) {
                                    border-left: 1px solid gray;
                                    padding-left: 20px;
                                }
                                
                                > ul {
                                    margin: 0;
                                    padding: 0;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: flex-start;
                                    align-items: flex-start;
                                    gap: 10px;

                                    h5 {
                                        font-weight: bold;
                                    }

                                    > li {
                                        // border: 1px solid violet;
                                        a {
                                            font-size: 16px;
                                        }
                                    }
                                }
                            }
                            // End pro-collection

                            // start products-images
                            .products-images {
                                padding-top: 20px;
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                                gap: 20px;

                                .pro-img {
                                    position: relative;

                                    img {
                                        width: 100%;
                                        max-width: 100%;
                                    }

                                    .pro-banner {
                                        // border: 1px solid red;
                                        position: absolute;
                                        right: 0;
                                        top: 50%;
                                        transform: translateY(-50%);
                                        padding-left: 20px;
                                        padding-right: 10px;

                                        > a {
                                            text-decoration: none;
                                            text-transform: capitalize;
                                            color: #000;
                                            position: relative;
                                            padding-bottom: 5px;
                                            transition: .5s;

                                            &:hover {
                                                color: #FF324D;
                                                &::after {
                                                    width: 100%;
                                                }
                                            }

                                            &::after {
                                                content: "";
                                                position: absolute;
                                                bottom: 0;
                                                left: 0;
                                                transition: 0.5s;
                                                height: 1.5px;
                                                width: 20%;
                                                border-radius: 10px;
                                                background-color: #000;
                                            }
                                        }
                                    }
                                }
                            }
                            // End products-images
                        }
                    }
                    // End products links

                    // Start blog links
                    .blog-con {
                        position: relative;

                        &:hover {
                            .blog-links {
                                bottom: -180px;
                                opacity: 1;
                                visibility: visible;
                            }
                        }

                        .blog-links {
                            position: absolute;
                            bottom: -290px;
                            left: -10px;
                            width: 200px;
                            background-color: #fff;
                            opacity: 0;
                            visibility: hidden;
                            transition: .3s;
                            box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);

                            > ul {
                                margin: 0;
                                padding: 10px;
                                display: flex;
                                flex-direction: column;
                                justify-content: flex-start;
                                align-items: flex-start;
                                gap: 10px;

                                > li {
                                    width: 100%;
                                    display: flex;
                                    justify-content: space-between;
                                    align-items: center;
                                    gap: 20px;

                                    .caret-right {
                                        transition: .3s;
                                    }

                                    &:hover {
                                        .caret-right {
                                            transform: rotate(180deg);
                                            color: $header-hover-color;
                                        }
                                    }
                                }

                                // Start lists
                                .grids,
                                .masonry,
                                .single,
                                .list {
                                    position: relative;

                                    &:hover {
                                        .grids-list, 
                                        .masonry-list,
                                        .single-list,
                                        .list-list {
                                            opacity: 1;
                                            visibility: visible;
                                            top: 0;
                                        }
                                    }

                                    .grids-list, 
                                    .masonry-list,
                                    .single-list,
                                    .list-list {
                                        position: absolute;
                                        top: 10px;
                                        background-color: #fff;
                                        visibility: hidden;
                                        opacity: 0;
                                        transition: .3s;

                                        > ul {
                                            margin: 0;
                                            padding: 10px;
                                            display: flex;
                                            flex-direction: column;
                                            justify-content: flex-start;
                                            align-items: flex-start;
                                            gap: 10px;

                                            > li {
                                                width: 100%;
                                            }
                                        }
                                    }
                                }
                                // End lists

                                // start .grids-list
                                .grids-list {
                                    left: -235px;
                                }
                                // End .grids-list

                                // Start masonry list
                                .masonry {
                                    .masonry-list {
                                        left: -150px;
                                    }
                                }
                                // End single list
                                
                                // Start single list
                                .single {
                                    .single-list {
                                        left: -130px;
                                    }
                                }
                                // End single list
                                
                                // Start list list
                                .list {
                                    .list-list {
                                        left: -150px;
                                    }
                                }
                                // End list list
                            }
                        }
                    }
                    // End blog links

                    // Start shop links
                    .shop-con {
                        position: relative;

                        &:hover {
                            .shop-links {
                                bottom: -285px;
                                opacity: 1;
                                visibility: visible;
                            }

                            h5 {
                                color: #000;
                            }
                        }

                        .shop-links {
                            position: absolute;
                            bottom: -290px;
                            left: -600px;
                            width: 950px;
                            background-color: #fff;
                            opacity: 0;
                            visibility: hidden;
                            transition: .3s;
                            padding: 20px;
                            box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);

                            // Start pro-collection
                            .shop-collection {
                                display: grid;
                                grid-template-columns: repeat(auto-fill , minmax(210px , 1fr));
                                gap: 10px;

                                > ul:not(:first-child) {
                                    border-left: 1px solid gray;
                                    padding-left: 20px;
                                }
                                
                                > ul {
                                    margin: 0;
                                    padding: 0;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: flex-start;
                                    align-items: flex-start;
                                    gap: 10px;

                                    h5 {
                                        font-weight: bold;
                                    }

                                    > li {
                                        a {
                                            font-size: 16px;
                                        }
                                    }
                                }

                                .new-sale {

                                    img {
                                        width: 100%;
                                        max-width: 100%;
                                    }
                                }
                            }
                            // End pro-collection
                        }
                    }
                    // End shop links
                }
            }
            // End nav

            // start search-cart
            .search-cart {
                display: flex;
                gap: 25px;
                align-items: center;
                list-style: none;
                margin: 0;
                padding: 0;

                a {
                    text-decoration: none;
                    color: #000;
                    transition: .5s;

                    &:hover {
                        color: $header-hover-color;
                    }
                }

                li {
                    cursor: pointer;
                    transition: .5s;

                    &:hover {
                        color: $header-hover-color;
                    }
                }

                :nth-child(2) {
                    position: relative;
                    span {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        top: -5px;
                        right: -10px;
                        min-width: 16px;
                        min-height: 16px;
                        color: #fff;
                        background-color: #FF324D;
                        border-radius: 50%;
                        font-size: 11px;
                        padding: 0 3px;
                    }
                }

                #list-icon {
                    display: none;
                }
            }
            // End search-cart
        }
        // End container
    }
    // End bottom-header div



    // Start Media for header-bottom
    @media (max-width: 992px) {

        .bottom-header {

            .container {
                position: relative;

                nav {
                    display: none;

                    background-color: #fff;
                    position: absolute;
                    top: 120%;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 100%;


                    .home-links,
                    .Pages-links {
                        left: 15% !important;
                        bottom: -270px !important;
                        z-index: 1;
                        width: calc(100% - 15%) !important;
                        box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);
                    }

                    .home-con,
                    .pages-con {
                        &:hover {
                            .home-links,
                            .Pages-links {
                                bottom: -250px !important;
                            }
                        }
                    }

                    .products-links {
                        top: 100% !important;
                        left: 15% !important;
                        z-index: 1;
                        width: calc(100% - 15%)!important;
                        box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);
                        height: fit-content;

                        .pro-collection {
                            display: flex !important;
                            flex-direction: column;
                            gap: 40px !important;

                            > ul {
                                border: none !important;
                                padding: 0 !important;
                            }
                        }
                        
                        .products-images {
                            display: none !important;
                        }
                    }

                    .blog-links {
                        top: 100% !important;
                        left: 15% !important;
                        z-index: 1;
                        width: calc(100% - 15%) !important;
                        box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);
                        height: fit-content;
                        transition: .1s !important;

                        > ul > li {
                            transition: .3s;
                            padding: 0 10px;
                            border-radius: 10px;

                            &:hover {
                                background-color: beige;
                            }
                        }

                        .grids-list,
                        .masonry-list,
                        .single-list,
                        .list-list {
                            top: 100% !important;
                            left: 20% !important;
                            z-index: 1;
                            width: calc(100% - 20%) !important;
                            box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);
                            height: fit-content;

                            > ul > li {
                                transition: .3s;
                                padding: 0 10px;
                                border-radius: 10px;
    
                                &:hover {
                                    background-color: cornsilk;
                                }
                            }
                        }
                    }

                    .shop-links {
                        top: 100% !important;
                        left: 15% !important;
                        z-index: 1;
                        width: calc(100% - 15%) !important;
                        box-shadow: 0 13px 42px 11px rgba(0,0,0,.05);
                        height: fit-content;
                        transition: .1s !important;

                        .shop-collection {
                            display: flex !important;
                            flex-direction: column;
                            gap: 40px !important;

                            > ul {
                                border: none !important;
                                padding: 0 !important;
                            }

                            .new-sale {
                                display: none;
                            }
                        }
                    }

                    > ul {
                        flex-direction: column;
                        align-items: flex-start;

                        > li {
                            width: 100%;
                            transition: .5s;

                            &:hover {
                                background-color: #292b2c17;
                            }

                            a {
                                width: 100%;
                                justify-content: space-between;
                                align-items: center;
                            }
                        }
                    }
                }

                .search-cart {
                    #list-icon {
                        display: block;
                    }
                }
            }
        }
    }
    // End Media for header-bottom


    // start search div 
    .search-contrainer {
        background-color: #1a1e1d9c;
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .5s;
        z-index: 100;

        .input-con {
            width: 80%;
            height: 200px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;

            .close {
                position: absolute;
                top: -50px;
                right: 50px;
                color: #fff;
                cursor: pointer;
            }
            .search-input {
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: relative;
                width: 90%;
                padding-bottom: 10px;

                &:after {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 3px;
                    background-color: #fff;
                    bottom: 0;
                    left: 0;
                    border-radius: 6px;
                }

                input {
                    border: none;
                    outline: none;
                    background-color: transparent;
                    width: 95%;
                    color: #fff;
                    font-size: 1.3rem;

                    &::placeholder {
                        color: #fff;
                    }
                }

                .search-icon {
                    color: #fff;
                    cursor: pointer;
                    transition: .5s;

                    &:hover {
                        color: aqua;
                    }
                }
            }
        }
    }
    // End search div 
}
// End header----------------------------------------------------------




// Start tesmoinal Slider-------------------------------------------------

    // start swiper-----------
    .testominal {
            height: calc(100vh - 120px);
    
            .slide-item {
                
                .slide-1, 
                .slide-2, 
                .slide-3 {
                    height: 100%;
                    background-position: center !important;
                    background-repeat: no-repeat !important;
                    background-size: cover !important;
                    position: relative;
    
                    .item-head {
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 30px;
                        width: 600px;

                        p {
                            font-size: 20px;
                        }
    
                        h5 {
                            font-weight: 300;
                            margin-bottom: 1rem;
                        }
    
                        h2 {
                            font-size: 60px;
                            margin-bottom: 20px;
                            font-weight: 600;
                        }
    
                        a {
                            text-decoration: none;
                            display: block;
                            width: fit-content;
                            color: #FF324D;
                            border: 1px solid #FF324D;
                            border-radius: 10px;
                            position: relative;
                            padding: 10px 20px;
                            z-index: 19;
                            overflow: hidden;
                            transition: .4s;
                            font-size: 20px;
    
                            &:hover {
                                color: #fff;
    
                                &::after,
                                &::before {
                                    width :60%;
                                }
                            } 
    
                            &::after {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                left: 0;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .4s;
                            }
    
                            &::before {
                                content: "";
                                position: absolute;
                                width: 0;
                                height: 100%;
                                top: 0;
                                right: 0;
                                background-color: #FF324D;
                                z-index: -1;
                                transition: .4s;
                            }
                        }
                    }
                }
    
                .slide-1 {
                    background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner9.jpg");
                }
                .slide-2 {
                    background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner8.jpg");
                }
                .slide-3 {
                    background: url("https://bestwebcreator.com/shopwise/demo/assets/images/banner7.jpg");
                }
            }

            .swiper-pagination {

                span {
                    width: 15px;
                    height: 15px;
                    background-color: #FF324D;
                    margin: 0 15px !important;
                    position: relative;

                    &::after {
                        content: "";
                        position: absolute;
                        width: 25px;
                        height: 25px;
                        border-radius: 50%;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50% , -50%);
                        border: 1px solid red;
                    }
                }
            }
    
            .swiper-button-prev,
            .swiper-button-next {
                display: none;
            }
    }
    // End swiper-----------

        // start media for swiper
        @media (max-width : 992px) {
            .testominal {
                height: 50vh;

                .slide-item {
                    height: 50vh;
                    
                    .item-head {
                        left: 10% !important;

                        h2 {
                            font-size: 50px;
                        }

                        p {
                            font-size: 16px !important;
                            width: 400px;
                        }
                    }
                }
            }
        }

        @media (max-width : 768px) {
            .testominal {
                height: 40vh;

                .slide-item  {
                    height: 40vh;
                    
                    .item-head {
                        left: 6% !important;

                        h2 {
                            font-size: 40px !important;
                            width: 350px !important;
                        }

                        p {
                            font-size: 16px !important;
                            width: 350px;
                        }

                        a {
                            font-size: 16px !important;
                        }
                    }
                }
            }
        }

        @media (max-width : 580px) {
            .testominal {
                height: 30vh;

                .slide-item  {
                    height: 30vh;
                    
                    .item-head {
                        left: 4% !important;

                        p {
                            display: none;
                        }

                        h2 {
                            font-size: 25px !important;
                            width: 220px !important;
                        }

                        h6 {
                            font-size: 16px !important;
                            width: 280px;
                        }

                        a {
                            font-size: 13px !important;
                            padding: 10px 20px !important;
                        }
                    }
                }

                .swiper-pagination {

                    span {
                        width: 10px !important;
                        height: 10px !important;
    
                        &::after {
                            width: 15px !important; 
                            height: 15px !important;
                        }
                    }
                }

                .swiper-button-prev,
                .swiper-button-next {
                    display: none;
                }
            }
        }

        @media (min-width:1200px) {
            .item-head {
                width: 50% !important;

                h6 {
                    font-weight: 300;
                    font-size: 2rem !important;
                    width: 100%;
                }

                h2 {
                    font-size: 4rem !important;
                }

                p {
